import './ContactForm.scss';

import emailjs from 'emailjs-com';
import RoshniVertical from '../../assets/perfil.jpg';

const ContactForm = () => {

    
    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_yp79r5e', 'template_yvmf1lb', e.target, 'user_EZglJM3rUj27o2f8bpk7T')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset();
    }

    return(
        <section className="Contact__form-section">
                <form 
                        onSubmit={sendEmail} 
                        className="Contact__form"
                        >
                            <h3>Contacto</h3>
                            <input 
                                className="Contact__form-name" 
                                type="text" 
                                placeholder="Nombre y Apellidos"
                                name="nombre"
                            ></input>
                            <input 
                                className="Contact__form-name" 
                                type="email" 
                                placeholder="email@ejemplo.com"
                                name="email"
                            ></input>
                            <textarea 
                                rows="5"
                                className="Contact__form-msg"
                                type="text" 
                                placeholder="Escribe aquí tu mensaje"
                                name="mensaje"
                            ></textarea>
                            <input className="Contact__form-submit" type="submit" name="submit" value="Enviar"></input>
                </form>
                <img src={RoshniVertical} className="About__img" alt="Roshcv"/>
            </section>

    )
}

export default ContactForm;