
import React from 'react';
import emailjs from 'emailjs-com';

import './Footer.scss';
import LogoR from '../../assets/R_Verde.png';
import LogoTitle from '../../assets/logo_verde_letras.png';
import linkedin from '../../assets/icons/linkedin.svg';
import doctoralia from '../../assets/icons/doctoraliawhite.png';
import instagram from '../../assets/icons/instagram.svg';

function Footer () {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_yp79r5e', 'template_yvmf1lb', e.target, 'user_EZglJM3rUj27o2f8bpk7T')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset();
    }
    
    return (
        
        <div className='Footer'>
            <div className="Footer__content">
                <div className="Footer__r">
                    <img src={LogoR} alt="footer_r" />
                </div>
                <ul className="Footer__menu">
                    <li class="list-item"><a href="/" rel="noopener noreferrer">CONÓCEME</a></li>
                    <li class="list-item"><a href="/" rel="noopener noreferrer">PUBLICACIONES</a></li>
                    <li class="list-item"><a href="/" rel="noopener noreferrer">SERVICIOS</a></li>
                    <li class="list-item"><a href="/" rel="noopener noreferrer">CONTACTO</a></li>
                </ul>
                <div className="Footer__logotitle">
                    <img src={LogoTitle} alt="footer_logotitle" />
                </div>
                <div className="Footer__social">
                    <a href="https://www.instagram.com/lolosni/?hl=es" target="_blank" rel="noopener noreferrer" className="social-item"><img src={instagram} alt="instagram"/></a>
                    <a href="https://www.linkedin.com/in/roshni-peswani-8057604b/?originalSubdomain=es" target="_blank" rel="noopener noreferrer" className="social-item"><img src={linkedin} alt="linkedin"/></a>
                    <a href="https://www.doctoralia.es/roshni-peswani-peswani/dietista-nutricionista/maspalomas" target="_blank" rel="noopener noreferrer" className="social-item"><img src={doctoralia} alt="doctoralia"/></a>
                </div>
                <form 
                onSubmit={sendEmail} 
                className="Footer__form"
                >
                    <h3>Contacto</h3>
                    <input 
                        className="Footer__form-name" 
                        type="text" 
                        placeholder="Nombre y Apellidos"
                        name="nombre"
                    ></input>
                    <input 
                        className="Footer__form-name" 
                        type="email" 
                        placeholder="email@ejemplo.com"
                        name="email"
                    ></input>
                    <textarea 
                        rows="5"
                        className="Footer__form-msg"
                        type="text" 
                        placeholder="Escribe aquí tu mensaje"
                        name="mensaje"
                    ></textarea>
                    <input className="Footer__form-submit" type="submit" name="submit" value="Enviar"></input>
                </form>
            


            </div>
        </div>



    );
}

export default Footer;