import React from 'react';

import './Testimonials.scss';
import rightQuote from '../../assets/icons/rightquote.png';





export default function Testimonials({ testimonials }) {
  
  const [count, setCount] = React.useState(0);
  
  // let previousState = [];

  function generateRandom (){
    let maxNumber = testimonials.length;
    let randomNumber = Math.floor(Math.random() * maxNumber);
    // previousState.push(randomNumber);

    setCount(randomNumber);
  }

 

  
  return (
      <div className="TestimonialsBackground">
        <div className="TestimonialsContainer">
          <div className="quotes">
            <img src={rightQuote} alt="right quote"></img>
          </div>
          <h4 className="selectedName">
            {testimonials[count].author}
          </h4>
          <p className="selectedTestimonial">
            {testimonials[count].testimonial}
          </p>
          <button className="button"onClick={generateRandom}>Sorpréndeme</button>
          <p className="doctoralia">Testimonios extraidos del perfil profesional de Roshni Peswani alojado en la web <a href="https://www.doctoralia.es/roshni-peswani-peswani/dietista-nutricionista/maspalomas">www.doctoralia.es</a></p>
        </div>
      </div>

    
  )


}

