
import './CV.scss';

import Foto1 from '../../assets/foto1.jpg';
import Foto2 from '../../assets/foto2.jpg';

const CV = () => {
    return(
        <div className="CV__container">
            <div className="CV__foto1">
                <img src={Foto1} alt="foto1"/>
            </div>
            <div className="CV__foto2">
                <img src={Foto2} alt="foto2"/>
            </div>
            <div className="CV__text">
                <h1>Conóceme</h1>
                <h2>
                    Soy Roshni, una apasionada de la vida sana con más de 10 años de experiencia como nutricionista/dietista
                </h2>
                <p>
                Leonesa de nacimiento, durante 11 años Catalana y actualmente asentada junto con mi compañero de viaje Jesús Sanchis, en un precioso pueblo Valenciano: Quartell. Salgo de Barcelona, renuncio a un trabajo fijo, a un cargo importante, renuncio a ello buscando tranquilidad, naturaleza, monte, mar, el bienestar físico pero, sobre todo, el emocional.

                Es por ello que todo empezó cuando nací o, mejor dicho, antes de nacer. Mi madre, Olga Cuevas, ha sido siempre mi referente. Como ella dice en la dedicatoria de su libro El equilibrio a través de la alimetación, dedicado a “Marta y Lucía, mi laboratorio viviente”. Así fue, y me siento muy orgullosa de ello. Mi hermana Marta y yo, fuimos criadas en coherencia con lo que pauta la naturaleza. Crecimos siempre a base de alimentos saludables y solucionamos malestares con los tratamientos naturales que podéis leer en nuestro libro "Remedios naturales al alcance de todos". De acuerdo con el dictamen de nuestra evolución, crecimos sin alimentos azucarados ni procesados.
                </p>
            </div>
        </div>
    )
}

export default CV;





