import './Logos.scss';

// Images //
import CardenalHerrera from '../../assets/logos_bio/cardenalherreralogo.svg';
import CentroCime from '../../assets/logos_bio/centrocime.png';
import Complutense from '../../assets/logos_bio/complutense.png';
import Fresenius from '../../assets/logos_bio/freseniuskabi.svg';
import Uned from '../../assets/logos_bio/unedlogo.svg';
import SanRoque from '../../assets/logos_bio/hospitalessanroque.png';
import Hps from '../../assets/logos_bio/HPS.jpg';

const Logos = () => {

    return (
        <div className="logos">
                <img className="logo__img" src={SanRoque} alt="San Roque"/>
                <img className="logo__img" src={Hps} alt="Hps"/>
                <img className="logo__img" src={CentroCime} alt="Centro Cime"/>
                <img className="logo__img" src={Fresenius} alt="Fresenius"/>
                <img className="logo__img" src={CardenalHerrera} alt="CH"/>
                <img className="logo__img" src={Uned} alt="Uned"/>
                <img className="logo__img" src={Complutense} alt="Complutense"/>
        </div>  
    )
}

export default Logos;